<template>
  <div>
    <da-manage-page>
      <div slot="button">
        <el-button icon="my-zengjia" @click="dialogVisible=true;">新增</el-button>
      </div>
      <el-form slot="search" :inline="true" :model="searchForm.formdata">
        <el-form-item label="企业名称">
          <el-input v-model="searchForm.formdata.name"></el-input>
        </el-form-item>

        <el-form-item label="企业简称">
          <el-input v-model="searchForm.formdata.abbreviation"></el-input>
        </el-form-item>

        <el-form-item>
          <el-button type="primary" @click="getTbList">查询</el-button>
        </el-form-item>
      </el-form>
      <da-table slot="content" :colums="colsData" :table-list="tableList" :search-data="searchForm" @callBack="getTbList" :showSelection="false">

        <el-table-column property="operation" label="操作" width="150" slot="operation" align="center" fixed="right">
          <template slot-scope="scope">
            <el-button size="small" @click="editTb(scope.row)">编辑</el-button>
            <el-button size="small" @click="delTb(scope.row)">删除</el-button>
          </template>
        </el-table-column>

      </da-table>
    </da-manage-page>

    <da-dialog :title="title" :visible.sync="dialogVisible" @close="closeDialog('addForm')">
      <el-form label-width="90px" :model="addForm" :rules="rules" ref="addForm">

        <el-form-item label="企业名称" prop="name">
          <el-input auto-complete="off" v-model="addForm.name"></el-input>
        </el-form-item>
        <el-form-item label="企业简称" prop="abbreviation">
          <el-input auto-complete="off" v-model="addForm.abbreviation"></el-input>
        </el-form-item>
        <el-form-item label="logo" prop="logo">
          <el-input auto-complete="off" v-model="addForm.logo"></el-input>
        </el-form-item>
        <el-form-item label="图片" prop="image">
          <!-- <el-input auto-complete="off" v-model="addForm.image"></el-input> -->
          <da-upload v-model="addForm.image" url="rest/dataUploadfile"></da-upload>
        </el-form-item>
        <el-form-item label="能耗统计开始时间" prop="statStartTime">
          <!-- <el-input auto-complete="off" v-model="addForm.statStartTime"></el-input> -->
          <el-date-picker v-model="addForm.statStartTime" type="date" placeholder="选择日期">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="能耗统计日" prop="statDay">
          <el-select v-model="addForm.statDay" filterable placeholder="请选择">
            <el-option v-for="item in statDayOptions" :key="item.dataValue" :label="item.dataText" :value="item.dataValue"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="告警邮件服务邮箱" prop="alarmEmailAddr">
          <el-input auto-complete="off" v-model="addForm.alarmEmailAddr"></el-input>
        </el-form-item>
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="saveDialog('addForm')">确 定</el-button>
        <el-button @click="dialogVisible = false">取 消</el-button>
      </div>
    </da-dialog>
  </div>
</template>
<script>
export default {
  name: "enterprise",
  data() {
    return {
      statDayOptions: [
        { dataText: "今日", dataValue: "今日" },
        { dataText: "昨日", dataValue: "昨日" }
      ],
      // 查询表单
      searchForm: {
        formdata: { name: "", abbreviation: "" },
        currentPage: 1,
        pageSize: 20
      },
      tableList: {}, // 数据列表
      colsData: [
        // 表头
        { label: "企业名称", prop: "name", align: "left", sortable: true },
        {
          label: "企业简称",
          prop: "abbreviation",
          align: "left",
          sortable: true
        },
        { label: "logo", prop: "logo", align: "left", sortable: true },
        { label: "图片", prop: "image", align: "left", sortable: true },
        {
          label: "能耗统计开始时间",
          prop: "statStartTime",
          align: "left",
          sortable: true
        },
        { label: "能耗统计日", prop: "statDay", align: "left", sortable: true },
        {
          label: "告警邮件服务邮箱",
          prop: "alarmEmailAddr",
          align: "left",
          sortable: true
        }
      ],
      dialogVisible: false,
      addForm: {
        name: "",
        logo: "",
        image: "",
        abbreviation: "",
        statStartTime: "",
        statDay: "",
        alarmEmailAddr: ""
      },
      rules: this.eachValidate({
        name: { required: true, maxString: "50" },
        logo: {},
        image: {},
        abbreviation: { maxString: "20" },
        statStartTime: { required: true },
        statDay: { maxString: "10" },
        alarmEmailAddr: { maxString: "50" }
      })
    };
  },
  computed: {
    title() {
      //弹窗标题
      return this.addForm.id ? "编辑企业资料管理" : "新增企业资料管理";
    },
    urlType() {
      //URL请求方式
      return this.addForm.id ? "put" : "post";
    }
  },
  watch: {},
  methods: {
    getTbList() {
      this.$ajax
        .get("rest/enterprise/page", { params: this.searchForm })
        .then(res => {
          if (res.data.status) {
            this.tableList = res.data;
          }
        });
    },
    editTb(row) {
      this.dialogVisible = true;
      this.addForm = JSON.parse(JSON.stringify(row));
    },
    closeDialog(formName) {
      this.$nextTick(function() {
        this.$refs[formName].resetFields();
        this.addForm = {
          name: "",
          logo: "",
          image: "",
          abbreviation: "",
          statStartTime: "",
          statDay: "",
          alarmEmailAddr: ""
        };
      });
    },
    saveDialog(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.$ajax[this.urlType]("rest/enterprise", this.addForm).then(
            res => {
              if (res.data.status) {
                this.$message.success(res.data.answerMsg);
                this.dialogVisible = false;
                this.getTbList();
              } else {
                this.$message.error(res.data.answerMsg);
              }
            }
          );
        }
      });
    },
    delTb(row) {
      this.$confirm("是否删除该记录?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(() => {
        this.$ajax
          .delete("rest/enterprise", { params: { id: row.id } })
          .then(res => {
            if (res.data.status) {
              this.$message.success(res.data.answerMsg);
              this.getTbList();
            } else {
              this.$message.error(res.data.answerMsg);
            }
          });
      });
    }
  },
  mounted() {
    this.getTbList();
  }
};
</script>



